import React from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import Icon16Down from "@vkontakte/icons/dist/16/down";
import Icon16Up from "@vkontakte/icons/dist/16/up";
import { formatCoins } from '../helpers';

const OperationsHistoryItem = ({
  senderId,
  targetId,
  amount,
  createdAt,
  formatDate,
  fetchedUser,
  senderName,
  targetName,
  senderPhoto,
  targetPhoto,
}) => {
  const photoURLTargetId = "https://vk.com/id" + targetId;
  const photoURLSenderId = "https://vk.com/id" + senderId;
  // const idVkTargetId = "https://vk.com/id" + targetId;
  // let now = new Date(createdAt);


  return (
    <>
      {senderId === fetchedUser.id ? (
        <Div className="card">
          <span style={{ paddingLeft: 3 }}></span>
          <Cell
            className="myItem"
            before={
              fetchedUser.photo_200 ? (
                <Link href={photoURLTargetId} target="_blank">
                  <Avatar src={targetPhoto} />
                </Link>
              ) : (
                  <Avatar src={"https://vk.com/images/camera_200.png"} />
                )
            }
            description={
              <Div style={{ marginLeft: 20 }}>
                <Div>
                  {fetchedUser && (
                    <Div style={{ wordWrap: "break-word", marginBottom: 5 }}>
                      {targetName}
                    </Div>
                  )}
                </Div>

                <Div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    flexDirection: "row",
                    justifyContent: "start",
                    margin: 0,
                    padding: 0,
                  }}
                >
                  <Div style={{ color: "red" }}>- {formatCoins(amount)} </Div>
                  <Div className="iconDC" style={{ marginLeft: 8 }} />
                </Div>
              </Div>
            }
            asideContent={
              <Div className="divFormatDate">{formatDate(createdAt)}</Div>
            }
          // before={<Icon24FavoriteOutline/>}
          // indicator={rating}
          >
            <Div className="divOperationsHistory">
              <Div>
                <Icon16Up fill="red" />
              </Div>
              <Div style={{ marginTop: 4, marginLeft: 4 }}>Перевод</Div>
            </Div>
          </Cell>
        </Div>
      ) : (
          <Div className="card">
            <span style={{ paddingLeft: 3 }}></span>
            <Cell
              className="myItem"
              before={
                senderPhoto ? (
                  <Link href={photoURLSenderId} target="_blank">
                    <Avatar src={senderPhoto} />
                  </Link>
                ) : (
                    <Avatar src={"https://vk.com/images/camera_200.png"} />
                  )
              }
              description={
                <Div
                  style={{
                    marginLeft: 20,
                  }}
                >
                  <Div>
                    <Div style={{ wordWrap: "break-word", marginBottom: 5 }}>
                      {senderName}
                    </Div>
                  </Div>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      flexDirection: "row",
                      justifyContent: "start",
                      margin: 0,
                      padding: 0,
                    }}
                  >
                    <Div style={{ color: "green" }}>+ {formatCoins(amount)}</Div>
                    <Div className="iconDC" style={{ marginLeft: 8 }} />
                  </Div>
                </Div>
              }
              asideContent={
                <Div className="divFormatDate">{formatDate(createdAt)}</Div>
              }
            >
              <Div className="divOperationsHistory">
                <Div>
                  <Icon16Down fill="green" />
                </Div>
                <Div>Пополнение</Div>
              </Div>
            </Cell>
          </Div>
        )}
    </>
  );
};

export { OperationsHistoryItem };
