import React, { useEffect, useState } from "react";
import {
  Group,
  Cell,
  Div,
  InfoRow,
  Input,
  Button,
} from "@vkontakte/vkui";

import {
  XAxis,
  YAxis,
  Tooltip,
  CartesianGrid,
  AreaChart,
  Area,
} from "recharts";

import {
  addGroup,
} from "../bridge-method";

const ExchangeCourse = ({
  go,
  id,
  durkaCoins,
  course,
  addExchangeOffer,
  onChangeAction,
  costOneDC,
  inputDurkaCoins,
  qiwiAccount,
  openInfoVIP,
  getPlatform,
  chart,
  openModalCalendar
}) => {

  const localCourse = `DC/RUB ${course} ₽`;
  let width = 0;
  const [widthMobile, setWidthMobile] = useState(0);

  useEffect(() => {
    width = window.innerWidth > 0 ? window.innerWidth : window.screen.width;
    setWidthMobile(Math.round(width * 0.92));

    setTimeout(() => {
      addGroup(true,);
    }, 5000);
  }, []);

  return (
    <>
      <Div
        style={{
          margin: 10,
        }}
      >
        <Group>
          <Cell>
            <InfoRow header="Ваш баланс" className="balanceDCMiddle">
              <Div className="divFlexStart">
                <Div className="balanceDCMiddle">{durkaCoins}</Div>
                <Div className="iconDC" style={{ marginLeft: 4 }} />
              </Div>
            </InfoRow>

            <InfoRow className="balanceDCMiddle" header="Текущий курс">
              <Div className="divFlexStart">
                <Div className="iconDC" style={{ marginRight: 4 }} />
                <Div className="balanceDCMiddle"> = {course}/₽ </Div>
              </Div>
            </InfoRow>

            <Div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
                flexDirection: "row",
                marginRight: "20px",
              }}
            >

              <Button
                style={{
                  backgroundColor: "#FFB800",
                }}
                mode="commerce"
                size="m"
                onClick={openModalCalendar}
              >
                Выбрать период
            </Button>
              <Div></Div>
            </Div>
          </Cell>
        </Group>

      </Div>
      <AreaChart

        width={
          getPlatform === "web" ? 580 : widthMobile
        }
        height={getPlatform === "web" ? 400 : 400}
        data={chart.concat({
          Курс: course,
          hour: "Сейчас",
        })}
        margin={{
          top: 30,
          right: 10,
          left: 0,
          bottom: 10,
        }}
      >
        <CartesianGrid
          strokeDasharray="3 3"
          fill="transparent "
        />
        <XAxis dataKey="hour" />
        <YAxis />
        <Tooltip />
        <Area
          type="monotone"
          dataKey="Курс"
          stroke="#82ca9d"
          fill="#82ca9d"

        />
      </AreaChart>
    </>
  );
};

export { ExchangeCourse };
