import React from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Div,
} from "@vkontakte/vkui";

import {
  GameItem,
} from "../gamesItem";

import { dataGames } from "../helpers";

const Games = ({
  id,
  go,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  updateConnection,
  socketReconnection,
  snackbar,

}) => {

  function goToApp() {
    bridge.send("VKWebAppOpenApp", { "app_id": 7559432, "location": "durkacoin" });
  }



  const renderGames = dataGames.map((item, index) => {
    return (
      <GameItem
        key={index}
        id={item.id}
        go={go}
        nameGame={item.nameGame}
        icon={item.icon}
        namePanelGame={item.namePanelGame}
        discription={item.discription}
        goToApp={goToApp}

      />
    );
  });


  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple left={<PanelHeaderBack onClick={go} data-to="home" />}>
        Игры
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
          <>
            {networkStatus ? (
              stub(
                "Отсутствует интернет - соединение!",
                updateConnection,
                "Повторить"
              )
            ) : (
                <>
                  {socketReconnection ? (
                    stub(
                      "Ошибка подключения к серверу",
                      socketReconnect,
                      "Повторить"
                    )
                  ) : (
                      <>
                        <Div
                          style={{
                            marginLeft: 10,
                          }}
                        >
                          {renderGames}
                        </Div>


                      </>
                    )}

                  {snackbar}
                </>
              )}
          </>
        )}
    </Panel>
  );
};

export { Games };
