import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Div,
  Button,
  Link,
  Avatar,
} from "@vkontakte/vkui";

import { IntuitionItem, ModalIntuitionResult } from "../gamesItem";
import { ModalIntuition } from "../modal";
import {
  formatCoins,
  SocketManager,
  delay,
} from "../helpers";
const { callMethod } = SocketManager;

let interval = null;

const Intuition = ({
  id,
  go,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  updateConnection,
  socketReconnection,
  snackbar,
  durkaCoins,
  openBuyDurkaCoins,
  setActiveModal,
  openAlertPay,
  openBase,
  openMenuVIP
}) => {
  let number = "";
  let numberResult = 10;
  // let globalBank = 0;
  let num = 0;
  let game = true;
  // let numberItem = 0;
  const [dataBet, setDataBet] = useState([]);
  const [bank, setBank] = useState(0);
  const [screenBank, setScreenBank] = useState("0");
  const [timer, setTimer] = useState(300);
  const [screenTimer, setScreenTimer] = useState("00:00");
  const [numberItem, setNumberItem] = useState(0);

  function convertTime(sec) {
    return (
      `${Math.floor(sec / 60)}`.padStart(2, 0) +
      ":" +
      `${sec % 60}`.padStart(2, 0)
    );
  }

  useEffect(() => {

    setTimeout(() => {
      callMethod("enterIntuition", {
        cancel: false,
      });
    }, 2000)

    SocketManager.socket.on("betsList2", (bets) => {
      if (!game) return;

      let b = 0;
      bets.forEach((bet) => {
        b += bet.amount;
      });
      // globalBank = b / 100_000;
      if (b !== 0) {
        setBank(b / 100_000);
      }
      setScreenBank((b / 100_000).toLocaleString());

      setDataBet(
        bets.map((bet) => {
          return {
            idVk: bet.user.id,
            name: bet.user.fullName,
            photoUrl: bet.user.photoURL,
            amount: bet.amount,
            inputNumber: bet.inputNumber,
            isVIP: bet.user.isVIP,
            vipNameColor: bet.user.vipNameColor
          };
        })
      );
    });

    SocketManager.socket.on("summarize2", ({ winners, randomNumber, bank }) => {
      game = false;
      // setStateGame(false);

      // console.log("Победители:", winners);
      numberResult = randomNumber;
      // setWinners(winners);
      // console.log("randomNumber", randomNumber);

      if (winners !== null) {
        let htmlWinners = winners.map((item, index) => {
          const userUrl = `https://vk.com/id${item.user.id}`;
          const name = item.user.fullName;
          return (
            <Div
              key={index}
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                flexDirection: "column",
                userSelect: "none",
              }}
            >
              <Div style={{ marginLeft: 10, marginRight: 10 }}>
                <Link href={userUrl} target="_blank">
                  <Avatar size={48} src={item.user.photoURL} />
                </Link>
              </Div>
              <Div>{name.split(" ")[0]}</Div>
              {/* <Div style={{whiteSpace: "nowrap", width: "70px", marginLeft: "10px"}}>{name}</Div> */}
            </Div>
          );
        });

        console.log("htmlWinners", htmlWinners);

        const timerId = setInterval(() => {
          if (num <= randomNumber) {
            // console.log("numberItem:", num);
            setNumberItem(num++);
          } else {
            num = 0;
            modalIntuitionResult(htmlWinners, bank);
            getBetsList(1);
            game = true;
            // setStateGame(true);
            clearInterval(timerId);
            delay(3000).then(() => {
              setNumberItem(0);
            });
            console.log("вызвали clearInterval");
          }
        }, 50);
      } else {
        num = 0;
        getBetsList(1);
        game = true;
        // setStateGame(true);
      }
    });

    getBetsList(2);
  }, []);

  useEffect(() => {
    if (interval) {
      console.log("Интервал уже есть!");
      clearInterval(interval);
    }

    let seconds = timer;

    setScreenTimer(convertTime(seconds));

    interval = setInterval(() => {
      seconds--;

      if (seconds <= 0) {
        clearInterval(interval);
      }

      setScreenTimer(convertTime(seconds));
    }, 1000);
  }, [timer]);

  function getBetsList(a) {
    callMethod("getIntuitionBetsList", a).then(({ message }) => {
      setTimer(0);
      setTimer(message.timer - 1); // Сколько осталось до итога в секундах

      let b = 0;
      message.bets.forEach((bet) => {
        b += bet.amount;
      });
      // globalBank = b / 100_000;
      if (b !== 0) {
        setBank(b / 100_000);
      }
      setScreenBank((b / 100_000).toLocaleString());

      setDataBet(
        message.bets.map((bet) => {
          return {
            idVk: bet.user.id,
            name: bet.user.fullName,
            photoUrl: bet.user.photoURL,
            amount: bet.amount,
            inputNumber: bet.inputNumber,
            isVIP: bet.user.isVIP,
            vipNameColor: bet.user.vipNameColor
          };
        })
      );
    });
  }

  function onChangeAction(e) {
    const value = e.target.value.trim();
    // setBet(+value);
    number = +value;
    console.log("Number.isInteger(number)", Number.isInteger(number));
  }

  const makeBet = () => {
    // console.log("typeof number", typeof number);
    if (number < 0 || number > 300) {
      setActiveModal(null);
      return openAlertPay(
        number < 0
          ? "Вы ввели число меньше 0, пилюлю забыл принять?"
          : "Вы ввели число больше 300, пилюлю забыл принять?",
        "red"
      );
    }
    if (Number.isInteger(number) === false) {
      setActiveModal(null);
      return openAlertPay(
        "Вы ввели не целое число, введите ЦЕЛОЕ число от 0 до 300",
        "red"
      );
    }
    if (isNaN(number)) {
      setActiveModal(null);
      return openAlertPay("Вы ввели не число, введите число от 0 до 300", "red");
    }

    setActiveModal(null);
    callMethod("addIntuitionBet", {
      inputNumber: number,
    }).then(({ status, message }) => {
      // setActiveModal(null);
      if (status === 'req_limit') return;
      openAlertPay(message, status === "denied" ? "red" : "green");
    });
  };

  const renderIntuition = dataBet.map((item, index) => {
    const userUrl = `https://vk.com/id${item.idVk}`;

    return (
      <IntuitionItem
        key={index}
        id={index + 1}
        name={item.name}
        photoUrl={item.photoUrl}
        userUrl={userUrl}
        number={number}
        go={go}
        inputNumber={item.inputNumber}
        isVIP={item.isVIP}
        vipNameColor={item.vipNameColor}
        openMenuVIP={openMenuVIP}
      />
    );
  });

  function openIntuitionModal() {
    setActiveModal(
      <ModalIntuition
        setActiveModal={setActiveModal}
        onChangeAction={onChangeAction}
        makeBet={makeBet}
      />
    );
  }

  const modalIntuitionResult = (a, bank) => {
    bank = (bank / 100_000).toLocaleString();
    setActiveModal(
      <ModalIntuitionResult
        setActiveModal={setActiveModal}
        onChangeAction={onChangeAction}
        openBase={openBase}
        bank={bank}
        numberResult={numberResult}
        renderItemResult={a}
      />
    );
  };

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
              justifyContent: "center",
            }}
          >
            <PanelHeaderBack
              onClick={(e) => {
                go(e);

                callMethod("enterIntuition", {
                  cancel: true,
                });
                SocketManager.socket.removeEventListener("betsList2");
                SocketManager.socket.removeEventListener("summarize2");
              }}
              data-to="games"
            />
            {/* <Div
              style={{
                color: "green",
              }}
            >
              {" "}
              Баланс: {formatCoins(durkaCoins)}
            </Div> */}

            <Button
              style={{
                backgroundColor: "green",
                marginLeft: "10px",
              }}
              mode="commerce"
              size="m"
              onClick={openBuyDurkaCoins}
            >
              Пополнить
            </Button>
            <Button
              style={{
                backgroundColor: "green",
                marginLeft: "10px",
              }}
              mode="commerce"
              size="m"
            // onClick={openBuyDurkaCoins}
            >
              {/* 00:{seconds} сек */}
              {screenTimer}
            </Button>
          </Div>
        }
      ></PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
          <>
            {networkStatus ? (
              stub(
                "Отсутствует интернет - соединение!",
                updateConnection,
                "Повторить"
              )
            ) : (
                <>
                  {socketReconnection ? (
                    stub(
                      "Ошибка подключения к серверу",
                      socketReconnect,
                      "Повторить"
                    )
                  ) : (
                      <Div
                        style={{
                          padding: 20,
                        }}
                      >
                        <FixedLayout
                          style={{
                            // display: "flex",
                            // alignItems: "space-between",
                            // flexDirection: "column",
                            // justifyContent: "center",
                            // backgroundColor: "white",
                            // height: "50px",
                            marginTop: 50,
                          }}
                          vertical="top"
                          filled
                        >
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "column",
                              justifyContent: "center",
                              height: "50px",
                            }}
                          >
                            <Div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                flexDirection: "row",
                                justifyContent: "center",
                                color: "green",
                                backgroundColor: "green",
                                borderRadius: "6px",
                                padding: "3px",
                                color: "white",
                                width: "95%",
                                height: "30px",
                                // marginRight: 10,
                              }}
                            >
                              <Div style={{ userSelect: "none" }}>
                                {" "}
                          Баланс: {formatCoins(durkaCoins)}
                              </Div>
                              <Div className="iconDC" style={{ marginLeft: 5 }} />
                            </Div>
                            {/* <Div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                          justifyContent: "center",
                          color: "green",
                          backgroundColor: "green",
                          borderRadius: "6px",
                          color: "white",
                          width: "200px",
                          height: "30px",
                          // marginRight: 10,
                          userSelect: "none"
                        }}
                      >
                        {" "}
                        00:{seconds} сек
                      </Div> */}
                          </Div>
                        </FixedLayout>

                        <Div
                          style={{
                            marginTop: "30px",
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                            justifyContent: "center",
                          }}
                        >
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "column",
                              justifyContent: "center",
                              marginBottom: "10px",
                              backgroundColor: "green",
                              borderRadius: "6px",
                              color: "white",
                              height: "30px",
                              width: "200px",
                            }}
                          >
                            Число победителя: {numberItem}
                          </Div>
                        </Div>

                        <Div style={{
                          marginTop: 30,
                          height: "100vh"
                        }}>{renderIntuition ? <>{renderIntuition}</> : ""}</Div>

                        <FixedLayout vertical="bottom" filled>
                          <Div
                            style={{
                              display: "flex",
                              alignItems: "center",
                              flexDirection: "column",
                              justifyContent: "space-evenly",
                              // marginBottom: 5,
                              height: "90px",
                              // backgroundColor: "white",
                            }}
                          >
                            <Div>
                              <Button
                                size="m"
                                // disabled={stateGame ? false : true}
                                // onClick={() => qiwiPay(price, id)}
                                onClick={() => openIntuitionModal()}
                                // data-to={namePanelGame}
                                style={{
                                  backgroundColor: "green",
                                  color: "white",
                                  borderRadius: "6px",
                                  width: "120px",
                                }}
                              >
                                Сделать ставку
                        </Button>
                            </Div>

                            <Div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                flexDirection: "row",
                                color: "white",
                                backgroundColor: "green",
                                width: "95%",
                                padding: "3px",
                                borderRadius: "6px",
                                height: "30px",
                              }}
                            >
                              <Div style={{ userSelect: "none" }}>
                                В банке: {screenBank}{" "}
                              </Div>
                              <Div className="iconDC" style={{ marginLeft: 5 }} />
                            </Div>
                          </Div>
                        </FixedLayout>
                      </Div>
                    )}

                  {snackbar}
                </>
              )}
          </>
        )}
    </Panel>
  );
};

export { Intuition };
