import React from "react";
import { useState } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
} from "@vkontakte/vkui";

import { InventoryItem, MiningInventoryItem } from "../components";
// import dataMining from "../helpers/dataMining";
import { dataMining } from "../helpers";


const Inventory = ({
  id,
  go,
  dataInventory,
  targetIdInventory,
  openUseItem,
  scheme,
  goHome,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  snackbar,
  dataMiners,
  miners,
  openDonutInfo,
  getPlatform
}) => {
  const [activeTab, setActiveTab] = useState("inventory");

  function tabInventory() {
    setActiveTab("inventory");
    window.scrollTo(0, 0);
  }

  function tabMaining() {
    setActiveTab("maining");
    window.scrollTo(0, 0);
  }

  const renderInventory = dataInventory.map((item, index) => {
    return (
      <InventoryItem
        key={index}
        id={item.id}
        name={item.name}
        icon={item.icon}
        count={item.count}
        openBuyItem={openBuyItem}
        targetIdInventory={targetIdInventory}
        openUseItem={openUseItem}
        scheme={scheme}
      />
    );
  });

  const renderMaining = dataMiners.map((item, index) => {
    const currentItem = dataMining.find(($item) => $item.id === item.id).levels[
      item.level - 1
    ];

    return (
      <MiningInventoryItem
        key={index}
        id={item.id}
        name={item.name}
        icon={item.icon}
        cps={currentItem.cps}
        scheme={scheme}
        level={item.level}
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={<PanelHeaderBack onClick={goHome} data-to="home" />}
      >
        Инвентарь
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
          <>
            <FixedLayout vertical="top" filled={true}>
              <Tabs>
                <TabsItem
                  onClick={tabInventory}
                  selected={activeTab === "inventory"}
                >
                  Имущество
              </TabsItem>
                <TabsItem onClick={tabMaining} selected={activeTab === "maining"}>
                  Майнеры
              </TabsItem>
              </Tabs>
            </FixedLayout>

            {networkStatus ? (
              stub(
                "Отсутствует интернет - соединение!",
                updateConnection,
                "Повторить"
              )
            ) : (
                <>
                  {socketReconnection ? (
                    stub(
                      "Ошибка подключения к серверу",
                      socketReconnect,
                      "Повторить"
                    )
                  ) : (
                      <>
                        {activeTab === "inventory" ? (
                          <>
                            {
                              renderInventory.length ? (
                                <>

                                  <Div style={{ marginTop: 55 }}>
                                    <Div className="iconDonut"
                                      style={{
                                        height: getPlatform === "web" ? "200px" : "140px"
                                      }}
                                    >
                                      <Div> </Div>
                                      <Div
                                        style={{
                                          display: "flex",
                                          alignItems: "center",
                                          flexDirection: "column",
                                          justifyContent: "center",
                                          backgroundColor: "green",
                                          color: "white",
                                          borderRadius: "7px",
                                          userSelect: "none",
                                          height: "30px",
                                          width: "90px",
                                          fontSize: "10px",
                                          margin: "0px 7px 7px 0px",
                                        }}
                                        onClick={go}
                                        data-to="donut"

                                      >
                                        Подробнее
                                       </Div>
                                    </Div>
                                    {renderInventory}
                                  </Div>
                                </>

                              ) : (
                                  <Div
                                    style={{
                                      marginTop: 55,
                                      display: "flex",
                                      alignItems: "center",
                                      justifyContent: "center",
                                      flexDirection: "row",
                                    }}
                                  >
                                    Пока у Вас нет имущества, чтобы купить перейдите в
                                    магазин.
                                  </Div>
                                )
                              // <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
                              //     <Spinner size="large" style={{ marginTop: 90 }} />
                              // </div>
                            }
                          </>
                        ) : (
                            ""
                          )}

                        {activeTab === "maining" ? (
                          <>
                            {renderMaining.length ? (
                              <Div style={{ marginTop: 55 }}>
                                <Div className="iconDonut"
                                  style={{
                                    height: getPlatform === "web" ? "200px" : "140px"
                                  }}
                                >
                                  <Div> </Div>
                                  <Div
                                    style={{
                                      display: "flex",
                                      alignItems: "center",
                                      flexDirection: "column",
                                      justifyContent: "center",
                                      backgroundColor: "green",
                                      color: "white",
                                      borderRadius: "7px",
                                      userSelect: "none",
                                      height: "30px",
                                      width: "90px",
                                      fontSize: "10px",
                                      margin: "0px 7px 7px 0px",
                                    }}
                                    onClick={go}
                                    data-to="donut"

                                  >
                                    Подробнее
                                       </Div>
                                </Div>
                                {renderMaining}
                              </Div>
                            ) : (
                                <Div
                                  style={{
                                    marginTop: 55,
                                    display: "flex",
                                    alignItems: "center",
                                    justifyContent: "center",
                                    flexDirection: "row",
                                  }}
                                >
                                  Пока у Вас нету майнеров, чтобы купить перейдите в
                                  магазин.
                                </Div>
                                // <div
                                //   style={{
                                //     display: "flex",
                                //     alignItems: "center",
                                //     flexDirection: "column",
                                //   }}
                                // >
                                //   <Spinner size="large" style={{ marginTop: 90 }} />
                                // </div>
                              )}
                          </>
                        ) : (
                            ""
                          )}
                      </>
                    )}

                  {snackbar}
                </>
              )}
          </>
        )}
    </Panel>
  );
};

export { Inventory };
