import bridge from "@vkontakte/vk-bridge";
//constants
import {
  GROUP_ID,
} from "../constants";


// разрешение на отправку сообщений от имени группы
export function subscribeMessageFromGroup(openAlertPay, setActiveTab) {
  bridge
    .send("VKWebAppAllowMessagesFromGroup", {
      group_id: GROUP_ID,
    })
    .then((res) => {
      // console.log("subscribeMessageFromGroup result", res);

    })
    .catch(err => {
      // console.log("subscribeMessageFromGroup result", err);

      setActiveTab("buy");
      openAlertPay(`Чтобы продолжить, разрешите отправку сообщений от имени группы`, "red");

    });
}


// подписка на группу 
export function addGroup(fromUseEffect, openAlertPay) {
  console.log("add group");

  bridge
    .send("VKWebAppJoinGroup", { group_id: GROUP_ID })
    .then(({ result }) => {
      console.log("VKWebAppJoinGroup", result);
      if (result && fromUseEffect !== true) {
        openAlertPay("Вы уже подписаны на группу");
      }
    });
}


// добавление сервиса в сообщество
export function AddToCommunity() {
    bridge.send("VKWebAppAddToCommunity", {});
  }