import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Div,
  Button
} from "@vkontakte/vkui";
import {
  XAxis,
  YAxis,
  Tooltip,
  CartesianGrid,
  AreaChart,
  Area,
} from "recharts";

import {
  SocketManager,
} from "../helpers";

const { callMethod } = SocketManager;

const DurkaCoinCrash = ({
  id,
  go,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  updateConnection,
  socketReconnection,
  snackbar,
  openBuyDurkaCoins,
  getPlatform,
}) => {

  let number = "";
  let numberResult = 10;
  let globalBank = 0;
  let min = 0;
  let max = 300;
  let num = 0;
  let game = true;
  // let numberItem = 0;
  const [dataBet, setDataBet] = useState([]);
  const [bank, setBank] = useState(0);
  const [screenBank, setScreenBank] = useState("0");
  const [timer, setTimer] = useState(300);
  const [screenTimer, setScreenTimer] = useState("00:00");
  const [numberItem, setNumberItem] = useState(0);
  const [randomNumber, setRandomNumber] = useState(0);
  const [winners, setWinners] = useState([]);
  const [renderItemResult, setRenderItemResult] = useState([]);
  const [stateGame, setStateGame] = useState(true);

  const resultRandomNumber = 6.40;
  const [dataChart, setDataChart] = useState([
    { time: "0s", y: 0 },
    { time: "3s", y: 0 },
    { time: "6s", y: 0 },
    { time: "10s", y: 0 },

  ]);




  // const data = [
  //   { value: "1x", y: 1 },
  //   { value: "2x", y: 1.5 },
  //   { value: "3x", y: 2 },
  //   { value: "4x", y: 4 },
  //   { value: "5x", y: 5 },

  // ];

  let data = [];


  let width = 0;
  const [widthMobile, setWidthMobile] = useState(0);

  useEffect(() => {
    width = window.innerWidth > 0 ? window.innerWidth : window.screen.width;
    setWidthMobile(Math.round(width * 0.92));

  }, []);


  function generateArr(min, max) {
    const amountRand = (Math.random() * (max - min) + min).toFixed(2);
    console.log("amountRand", amountRand);
    let n = 1;

    for (let index = 0; index < amountRand; index++) {

      // const randNumber = (Math.random() * (3 - 1) + 1).toFixed(1);

      n += 0.05;
      data.push(n);
    }

  }


  useEffect(() => {
    generateArr(1, 10);

    let index = 0;

    const interval = setInterval(() => {
      if (index === data.length) {
        clearInterval(interval);
      } else {
        // time = index === 0 ? 0 : time + 3;

        setDataChart(prev => [...prev,
        {
          ...prev[index],
          time: `${index + 1}s`,
          y: data[index],
        }]);

     

        index++;
      }


    }, 2000);



  }, []);



  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
              justifyContent: "center",
            }}
          >
            <PanelHeaderBack
              onClick={(e) => {
                go(e);

                callMethod("enterIntuition", {
                  cancel: true,
                });
                SocketManager.socket.removeEventListener("betsList2");
                SocketManager.socket.removeEventListener("summarize2");
              }}
              data-to="games"
            />


            <Button
              style={{
                backgroundColor: "green",
                marginLeft: "10px",
              }}
              mode="commerce"
              size="m"
              onClick={openBuyDurkaCoins}
            >
              Пополнить
            </Button>
            <Button
              style={{
                backgroundColor: "green",
                marginLeft: "10px",
              }}
              mode="commerce"
              size="m"
            // onClick={openBuyDurkaCoins}
            >
              {/* 00:{seconds} сек */}
              {screenTimer}
            </Button>
          </Div>
        }
      ></PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "Вы подключились с 2 устройств ",
          socketReconnect,
          "Переподключиться"
        )
      ) : (
          <>
            {networkStatus ? (
              stub(
                "Отсутствует интернет - соединение!",
                updateConnection,
                "Повторить"
              )
            ) : (
                <>
                  {socketReconnection ? (
                    stub(
                      "Ошибка подключения к серверу",
                      socketReconnect,
                      "Повторить"
                    )
                  ) : (
                      <Div
                        style={{
                          padding: "10px 0px 10px 0px",
                        }}
                      >

                        <AreaChart
                          width={
                            getPlatform === "web" ? 600 : widthMobile
                          }
                          height={getPlatform === "web" ? 350 : 220}
                          data={dataChart}
                          margin={{
                            top: 10, right: 0, left: 0, bottom: 30,
                          }}
                          // stackOffset="silhouette"
                          // baseValue="dataMin"
                        >
                          <CartesianGrid 
                          strokeDasharray="0" 
                          vertical={false}
                          // height={30}
                          />
                          <XAxis dataKey="time" />
                          <YAxis />
                          <Tooltip />
                          <Area type="basisOpen" dataKey="y" stroke="#8884d8" fill="#00635D" />
                        </AreaChart>

                        {/* <Div>{renderIntuition ? <>{renderIntuition}</> : ""}</Div> */}




                      </Div>
                    )}

                  {snackbar}
                </>
              )}
          </>
        )}
    </Panel>
  );
};

export { DurkaCoinCrash };
