import React, { useState } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Group,
  Header,
  Cell,
  Div,
  Input,
  Button,
  Checkbox,
  FixedLayout,
  Tabs,
  TabsItem,
} from "@vkontakte/vkui";

import { SocketManager, formatCoinsToServer } from "../helpers";
const { callMethod } = SocketManager;

const AdminPanel = ({ go, id, openAlertPay, snackbar }) => {
  const [DC, setDC] = useState("");
  const [userId, setUserId] = useState("");
  const [isBanned, setIsBanned] = useState(false);
  const [operation, setOperation] = useState(false);
  const [activeTab, setActiveTab] = useState("ban");

  function tab(type) {
    switch (type) {
      case "ban":
        setActiveTab(type);
        break;
      case "transfer":
        setActiveTab(type);
        break;
      default:
        break;
    }
  }

  function banUser() {
    if (!userId) {
      openAlertPay("Вы не ввели ссылку на пользователя", "red");
    }

    callMethod("banUser", {
      userId,
      isBanned,
    }).then(({ message, status }) => {
      if (status === 'req_limit') return;
      openAlertPay(message, status === "denied" ? "red" : "green");
    });
  }

  function transfer() {
    let durkaCoins = formatCoinsToServer(DC);
    callMethod('transfer', {
      durkaCoins: operation ? durkaCoins : -durkaCoins,
      userId
    }).then(({ message, status }) => {
      if (status === 'req_limit') return;
      openAlertPay(message, status === "denied" ? "red" : "green");
    })
  }

  function onChangeAction(e, type) {
    const value = e.target.value.trim();

    switch (type) {
      case "userId":
        setUserId(value);
        break;
      case "DC":
        setDC(value);
        break;
    }
  }

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple left={<PanelHeaderBack onClick={go} data-to="home" />}>
        Админ панель 3000
      </PanelHeaderSimple>

      <FixedLayout vertical="top" filled={true}>
        <Tabs>
          <TabsItem
            onClick={() => {
              tab("ban");
            }}
            selected={activeTab === "ban"}
          >
            Забанить
          </TabsItem>
          <TabsItem
            onClick={() => {
              tab("transfer");
            }}
            selected={activeTab === "transfer"}
          >
            Перевести
          </TabsItem>
        </Tabs>
      </FixedLayout>

      <Div>
        {activeTab === "ban" ? (
          <Group
            style={{ marginTop: "55px" }}
            header={<Header mode="secondary">Введите ссылку на шизоида</Header>}
          // separator="show"
          >
            <Cell>
              <Input
                // value={inputDurkaCoins}
                onChange={(e) => onChangeAction(e, "userId")}
                type="text"
              ></Input>
              <Div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "space-around",
                  flexDirection: "row",
                }}
              >
                <Checkbox
                  onChange={() => {
                    setIsBanned(true);
                  }}
                  checked={isBanned}
                >
                  Забанить
                </Checkbox>
                <Checkbox
                  onChange={() => {
                    setIsBanned(false);
                  }}
                  checked={!isBanned}
                >
                  Разбанить
                </Checkbox>
              </Div>

              <Button
                style={{
                  backgroundColor: "red",
                  color: "white",
                  marginTop: "10px",
                }}
                onClick={banUser}
                mode="commerce"
                size="xl"
                stretched
              >
                Выполнить
              </Button>
            </Cell>
          </Group>
        ) : (
            <>
              <Group
                style={{ marginTop: "55px" }}
                header={
                  <Header mode="secondary">Введите ссылку на шизоида</Header>
                }
                separator="hide"
              >
                <Cell>
                  <Input
                    // value={inputDurkaCoins}
                    onChange={(e) => onChangeAction(e, "userId")}
                    type="text"
                  ></Input>
                </Cell>
              </Group>
              <Group
                header={
                  <Header mode="secondary">Введите кол-во DurkaCoins</Header>
                }
              >
                <Cell>
                  <Input
                    // value={inputDurkaCoins}
                    onChange={(e) => onChangeAction(e, "DC")}
                    type="text"
                  ></Input>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "space-around",
                      flexDirection: "row",
                    }}
                  >
                    <Checkbox
                      onChange={() => {
                        setOperation(true);
                      }}
                      checked={operation}
                    >
                      Перевести DC
                  </Checkbox>
                    <Checkbox
                      onChange={() => {
                        setOperation(false);
                      }}
                      checked={!operation}
                    >
                      Снять DC
                  </Checkbox>
                  </Div>

                  <Button
                    style={{
                      backgroundColor: "red",
                      color: "white",
                      marginTop: "10px",
                    }}
                    onClick={transfer}
                    mode="commerce"
                    size="xl"
                    stretched
                  >
                    Выполнить
                </Button>
                </Cell>
              </Group>
            </>
          )}
      </Div>
      {snackbar}
    </Panel>
  );
};

export { AdminPanel };
